var util = require('./util.js');
var _ = require('lodash');

var exp = {
  chart: (
    parentSelector, svg, boundingBox, xPosition, yPosition,
    margin, width_full, height_full,
    sidepanel_affected, sidepanel_is_open,
    genders_flat, occupations_with_both_genders,
    click, yAxisLabel, for_sidepanel
  ) => {
    var width = width_full - margin.left - margin.right;
    var height = height_full - margin.top - margin.bottom;

    var xoff = 20;
    var circle_r = 12;

    var yoff_tooltip = 20;
    var xoff_tooltip = -circle_r/2;

    svg.append('g')
      .attr('name', 'axis_lefty')
      .attr('class', 'axis_lefty')
      .call(d3
        .axisLeft(yPosition)
        .tickFormat(x => `$${util.formatPrice(x)}`)
      );

    if(yAxisLabel){
      // text label for the y axis
      svg.append('text')
        .attr('transform', 'rotate(-90)')
        .attr('y', 0 - margin.left + 10)
        .attr('x', 0 - (height / 2))
        .attr('dy', '1em')
        .style('text-anchor', 'middle')
        .text('Median earning');
    }

    function redraw(){

      // svg
      var lines = svg.selectAll('.line')
        .data(occupations_with_both_genders) // redraw each time

      lines.exit().remove();

      lines
        .enter()
        .append('path')
        .attr('class', 'line')
        .attr('name', d => {
          return d.name
        })
        .attr('stroke', '#999')
        .attr('stroke-width', 4)
        .on('click', click)
        .attr('d', (d) => {
          var x = xPosition(d.male.occ_idx) + xoff;
          var pos_m = [x, yPosition(d.male.Value)];
          var pos_f = [x, yPosition(d.female.Value)];
          if(pos_m[1] < pos_f[1]){
            pos_m[1] += circle_r;
            pos_f[1] -= circle_r;
          }else if(pos_m[1] > pos_f[1]){
            pos_m[1] -= circle_r;
            pos_f[1] += circle_r;
          }
          return d3.line()([pos_m, pos_f])
        })
        .on('mouseover', chart_mouseover_l )
        // .on('mouseleave', chart_mouseleave )


      // -----------------------------------------------------------------------

      function tooltipsDOM(){
        var tooltipContainer = d3.select(parentSelector +' .tooltip_container');
        var qsl = document.querySelectorAll(parentSelector +' .tooltip_container');
        if(qsl.length == 0){
          tooltipContainer = d3.select(parentSelector)
            .append('div').classed('tooltip_container', true);
        }
        var tooltips = tooltipContainer.selectAll('.tooltip')
          .data(occupations_with_both_genders)

        tooltips.exit().remove();

        var g = tooltips
          .enter()
          .append('div')
          .attr('class', 'tooltip')
          .attr('name', d => {
            return d.name
          })
          .style('top', (d) => {
            var mt = margin.top;
            var y_male = yPosition(d.male.Value);
            var y_female = yPosition(d.female.Value);
            var y_lower = Math.max(y_male, y_female);
            var y_tooltip = y_lower + yoff_tooltip + mt + 10;
            return y_tooltip+'px';
          })
          .style('left', (d) => {
            var ml = margin.left;
            return (xPosition(d.occ_idx)+ xoff + ml + xoff_tooltip) + 'px';
          })

        if(!for_sidepanel){

          // sidepanel has the main labels on top, always visible
          g
            .append('span')
            .text(d => d.label || d.name)
            .attr('y', 5)
            .style('font-weight', 'bold')
            // .call(util.textWrap, 200)
            .style('width', 200)
        }else{

          tooltips
            .enter()
            .append('div')
            .attr('class', 'tooltip_always_vis')
            .text(d => d.label || d.name)
            .style('left', (d) => {
              var ml = margin.left;
              return (xPosition(d.occ_idx)+ xoff + ml + xoff_tooltip + 8) + 'px';
            })
            .style('font-weight', 'bold')
            // .call(util.textWrap, 200)
            .style('width', 200)
        }

        g
          .append('span')
          .html(d => 'Male: '+util.formatPrice(d.male.Value, true))

        g
          .append('span')
          .html(d => 'Female: '+util.formatPrice(d.female.Value, true))

        g
          .append('span')
          .html(d => 'Median: '+util.formatPrice(d.median.Value, true))

        // position within bounds
        g.each((d, i, nodes) => {
          _.forEach(nodes, (node) => {
            var bbox = node.getBoundingClientRect()
            var bound_right = boundingBox.x+boundingBox.width - 10;
            var bound_left = -10;
            if(for_sidepanel){
              bound_left = 20;
              bound_right += 50;
            }
            var x_shift;
            var current_left = parseFloat(d3.select(node).style('left'));
            if(bbox.x + bbox.width > bound_right){
              x_shift = (bound_right) - (bbox.x + bbox.width);
              d3.select(node)
                .style('left', (current_left+x_shift)+'px')
            }else if(bbox.x - boundingBox.x < bound_left){
              x_shift = bound_left - (bbox.x - boundingBox.x);
              if(for_sidepanel){
                x_shift -= 40;
              }
              d3.select(node)
                .style('left', (current_left+x_shift)+'px')
            }
          });
        })
      }
      // tooltipsSVG()
      tooltipsDOM()
      // -----------------------------------------------------------------------
      // circles (both Genders)


      var circles = svg.selectAll('.circle')
        .data(genders_flat)

      circles.exit().remove();

      circles
        .enter()
        .append('circle')
        .attr('class', d => 'circle circle--' + d.Gender)
        .attr('stroke', 'none')
        .attr('name', d => d.name)
        .attr('stroke-width', 1.5)
        .attr('fill', (d) => {
          if(d.Gender == 'Male'){
            return util.color_wage_gap_blue;
          }else if(d.Gender == 'Female'){
            return util.color_wage_gap_red
          }else{// median
            return '#999';
          }
        })
        .attr('r', d => d.Gender == 'median' ? 5 : circle_r)
        .style('opacity', d => d.Gender == '0.15!important' ? 5 : 'auto')
        .on('click', click )
        .on('mouseover', chart_mouseover )
        // .on('mouseleave', chart_mouseleave )
        .attr('cx', function(d, i) {
          return xPosition(d.occ_idx) + xoff
        })
        .attr('cy', function(d, i) {
          return yPosition(d.Value)
        })

      // Thick invisible lines for mouseover

      lines
        .enter()
        .append('path')
        .attr('class', 'line__formouse')
        .attr('name', d => {
          return d.name
        })
        .attr('stroke', 'black')
        .style('opacity', '0')
        .attr('stroke-width', 35)
        .on('click', click )
        .attr('d', (d) => {
          var x = xPosition(d.male.occ_idx) + xoff;
          var pos_m = [x, yPosition(d.male.Value)];
          var pos_f = [x, yPosition(d.female.Value)];
          var len_increase = 140;
          if(pos_m[1] < pos_f[1]){
            pos_m[1] -= len_increase;
            pos_f[1] += len_increase;
          }else if(pos_m[1] > pos_f[1]){
            pos_m[1] += len_increase;
            pos_f[1] -= len_increase;
          }
          return d3.line()([pos_m, pos_f])
        })
        .on('mouseover', chart_mouseover_l )
        .on('mouseleave', chart_mouseleave )


    }

    redraw();


    function chart_mouseover_l(d){
      chart_mouseover(d.name);
    }

    function chart_mouseover(d){
      var name;
      if(typeof d == 'string'){
        name = d
      }else{
        name = d['Indicator Name'];
      }

      var hover_accessor = 'Indicator Name';
      var hover_accessor_single = 'name';


      if(for_sidepanel){
        hover_accessor = 'Location';
        hover_accessor_single = 'Location';
      }

      // highlight this line, fade other lines
      svg.selectAll(' .circle').classed('circle--hover', (d, i) => {
        return (d[hover_accessor]) == name;
      }).classed('circle--fade', (d) => {
        return d[hover_accessor] !== name;
      });

      svg.selectAll(' .line').classed('line--hover', (d, i) => {
        return d[hover_accessor_single] == name;
      }).classed('line--fade', (d) => {
        return d[hover_accessor_single] !== name;
      });

      d3.selectAll(parentSelector+' .tooltip').classed('tooltip--hover', (d, i) => {
        return d[hover_accessor_single] == name;
      }).classed('tooltip--fade', (d) => {
        return d[hover_accessor_single] !== name;
      });

    }

    function chart_mouseleave(){
      // turn off hover and fade effects
      if(sidepanel_affected && sidepanel_is_open){
        exp.toggleGrayedOutMainChart(true);
      }else{
        d3.selectAll(parentSelector+' .circle')
          .classed('circle--hover', false)
          .classed('circle--fade', false);
        d3.selectAll(parentSelector+' .line')
          .classed('line--hover', false)
          .classed('line--fade', false);
      }
      d3.selectAll(parentSelector+' .tooltip').classed('tooltip--hover', false);
    }

  },

}
module.exports = exp;
