var util = require('./util.js');
var _ = require('lodash');
var Data = require('../src/data_access.js');
var cmp_vertiline = require('./cmp_vertiline_chart.js');
var cmp_tables = require('./cmp_tables.js');
var cmp_cross = require('./cmp_cross.js');

var exp = {
  is_open: false,

  open: (data, onclose) => {
    exp.onclose = onclose;
    exp.selected_occ = data;

    var sidepanel = d3.selectAll('.sidepanel')
      .classed('open', true)
      .select('h2')
      .text(data.label)

    d3.selectAll('.sidepanel .close')
      .on('click', exp.close)

    exp.chart(data);

    Data.people_count_by_occupation((count_data) => {
      var people_count_by_occupation = count_data;
      setTimeout(() => {
        cmp_tables.sidepanel_tables(data.data_occ, people_count_by_occupation);

        exp.cross_chart(data);
      }, 100);
    })

  },

  cross_chart: (group) => {
    var margin = {top: 20, right: 50, bottom: 50, left: 20},
      height_full = 320,
      width_full = 320,
      width = width_full - margin.left - margin.right,
      height = height_full - margin.top - margin.bottom;

    var selector = '.sidepanel .cross_chart';
    var draw_labels = false;


    var xCrossArrow = d3.scaleLinear()
        .range([0, width*0.8])
        .domain(group.xDomain)

    var yCrossArrow = d3.scaleLinear()
        .domain(group.yDomain)
        .range([height*0.8, 0])

    group.lines.forEach((line, i) => {
      line.x1 = width / 2;
      line.y1 = height / 2;
      line.y2 = yCrossArrow( line.y2_pct ) + (height*0.1);
      line.x2 = xCrossArrow( line.x2_pct ) + (width*0.1)
    });

    d3.select(selector).html('<div class="cross_chart_inner"><svg></svg></div>');
    cmp_cross.chart(selector+' svg', group.lines, margin, width_full, height_full, draw_labels);
  },

  close: () => {
    d3.select('.sidepanel')
      .classed('open', false)
    exp.is_open = false;
    if(exp.onclose) {
      exp.onclose();
    }
  },

  chart: (data) => {
  }

}

module.exports = exp;
