var util = require('./util.js');
var _ = require('lodash');

var exp = {
  sidepanel_tables: (data_of_occupation, people_count_by_occupation) => {
    var table = d3.select('.sidepanel table.earning')

    var locs = [{
      class: 'loc-us',
      data_loc: util.loc_us,
      loc_name: util.loc_us_name,
    }, {
      class: 'loc-state',
      data_loc: util.loc_state,
      loc_name: util.loc_state_name,
    }, {
      class: 'loc-city',
      data_loc: util.loc_city,
      loc_name: util.loc_city_name,
    }];


    var people_count_by_occupation_filtered = people_count_by_occupation.filter(
      (r) => r['Indicator Name'] == data_of_occupation.name )

    locs.forEach((loc) => {


      var table = d3.select('.sidepanel table.earning')


      var val_2010 = _.find(data_of_occupation.rows, {Year: 2010, Location: loc.data_loc });
      val_2010 = val_2010 ? val_2010.Value : NaN;
      var val_2018 = _.find(data_of_occupation.rows, {Year: 2018, Location: loc.data_loc });
      val_2018 = val_2018 ? val_2018.Value : NaN;

      var val_pct = util.percentChange(val_2010, val_2018);

      var loc_name = loc.loc_name;
      table.select('tr.'+loc.class).select('td:nth-child(1)').text(loc_name);

      table.select('tr.'+loc.class).select('td:nth-child(2)').text(util.formatPrice(val_2010, true));
      table.select('tr.'+loc.class).select('td:nth-child(3)').text(util.formatPrice(val_2018, true));
      table.select('tr.'+loc.class).select('td:nth-child(4)').text(util.formatPct(val_pct));


      var table_emp = d3.select('.sidepanel table.employees')
      //---------------
      table_emp.select('tr.'+loc.class).select('td:nth-child(1)').text(loc_name);

      var d_e = people_count_by_occupation_filtered
      val_2010 = _.find(d_e, {Year: 2010, Location: loc.data_loc });
      val_2010 = val_2010 ? val_2010.Value : NaN;
      val_2018 = _.find(d_e, {Year: 2018, Location: loc.data_loc });
      val_2018 = val_2018 ? val_2018.Value : NaN;

      val_pct = util.percentChange(val_2010, val_2018);

      table_emp.select('tr.'+loc.class).select('td:nth-child(2)').text(util.formatPrice(val_2010));
      table_emp.select('tr.'+loc.class).select('td:nth-child(3)').text(util.formatPrice(val_2018));
      table_emp.select('tr.'+loc.class).select('td:nth-child(4)').text(util.formatPct(val_pct));


    });
  }
}

module.exports = exp;
