var util = require('./util.js');
var sidepanel = require('./chart_occupation_trends_sidepanel.js');
var Data = require('../src/data_access.js');

var CHART_UPDATE_SPEED = 750; // animation/transition
var OCCUPATION_LINECHART_WIDTH_PCT = 0.2;
var MAIN_CHART_LOCATION = util.loc_city;


var exp = {
  init: function(){
    var page_template = require('../templates/chart_occupation_trends.html');
    d3.select('.page').html(page_template);
    sidepanel.is_open = false;
    exp.enter = null;
    exp.selected_occ = null;

    Data.get_occupations_wages(exp.chart);
  },
  chart: function(raw_data) {
    if(!raw_data){
      raw_data = exp.last_raw_data;
    }
    exp.last_raw_data = raw_data;

    var pageBox = d3.select('body').node().getBoundingClientRect(); //for svg use .getBBox();
    var headerBox = d3.select('.chart-tabs').node().getBoundingClientRect();
    var margin = {top: 90, right: 90, bottom: 25, left: 155},
      height_full = (pageBox.height),
      height = height_full - margin.top - margin.bottom;

    var width_full = pageBox.width;
    margin.right = Math.max(width_full * 0.1, 90);
    var width = width_full - margin.left - margin.right;

    if(sidepanel.is_open){
      var sidepanelBox = d3.select('.sidepanel').node().getBoundingClientRect();
      margin.right = Math.max(width_full * 0.18, 90);
      margin.top = 110;
      width_full = width = pageBox.width - sidepanelBox.width;
    }

    var rotate_axis = true;
    if(rotate_axis){
      OCCUPATION_LINECHART_WIDTH_PCT = 0.1;
      margin.top -= 50;
      margin.right -= 150;
    }
    var OCCUPATION_LINECHART_WIDTH = Math.min(OCCUPATION_LINECHART_WIDTH_PCT * width, 200);

    // empty previously drawn
    d3.select('.chart_main').html('')

    // append new svg
    var svg = d3.select('.chart_main')
      .append('svg')
        .attr('width', width_full)
        .attr('height', height_full);

    var prepped = Data.occupations_wages_prepped(raw_data);
    var occ_keys = prepped.occ_keys;
    var data_by_occupation_arr = prepped.data_by_occupation_arr;
    var lowestLastYearValue = prepped.lowestLastYearValue;
    var highestLastYearValue = prepped.highestLastYearValue;
    var data_by_occupation = prepped.data_by_occupation;
    var lowestDiffValue = prepped.lowestDiffValue;
    var highestDiffValue = prepped.highestDiffValue;

    // Data prep done -----------

    exp.setupSearch(occ_keys, data_by_occupation_arr);

    var scaleLastYearValue = d3.scaleLinear()
      .range([margin.left, width - margin.right])
      .domain([highestLastYearValue, lowestLastYearValue]);

    var year_idx_last = 8;
    var year_idx_half = year_idx_last/2;

    var scaleSubchartByYear = d3.scaleLinear()
        .domain([0, year_idx_last])
        .range([ 0, OCCUPATION_LINECHART_WIDTH ]);

    var linechart_half_height = 170;
    // based on first year value
    var smallYScale = d3.scaleLinear()
      .range([-linechart_half_height, linechart_half_height])
      .domain([highestLastYearValue, -highestLastYearValue]);

    var scaleOccupationChangePct = d3.scaleLinear()
      .rangeRound([height - margin.bottom, margin.top+30])
      .domain(
        d3.extent(d3.map(data_by_occupation).keys().map((occupation) => data_by_occupation[occupation].totalChangeDiff ))
      )

    if(rotate_axis){
      scaleLastYearValue
        .rangeRound([height-margin.top-100, 0])
      scaleOccupationChangePct
        .range([margin.left, width - margin.right])
    }

    var xLineCombined, yLineCombined;

    if(rotate_axis){
      xLineCombined = (opts, i, di) => {
        var totalChangeDiff = opts.totalChangeDiff;
        return scaleOccupationChangePct(totalChangeDiff) + scaleSubchartByYear(i)
      }

      yLineCombined = (opts, i, di) => {
        var latest = opts.latest;
        // adjust from last year value.. so it doesnt move much from scaleLastYearValue
        // var adjusted_di = latest - di
        // return height-scaleLastYearValue(latest) - y(adjusted_di);
        return height-scaleLastYearValue(latest) - smallYScale(di);
      }
    }else{
      // xLineCombined = (opts, i, di) => {
      //   var earliest = opts.earliest;
      //   return scaleLastYearValue(earliest) + scaleSubchartByYear(i)
      // }

      // yLineCombined = (opts, i, di) => {
      //   var totalChangeDiff = opts.totalChangeDiff;
      //   return scaleOccupationChangePct(totalChangeDiff) - y(di)*0.4;
      // }
    }
    var color = d3.scaleLinear()
      // .domain([-highestDiffValue, highestDiffValue])
      .domain([lowestDiffValue, highestDiffValue]) // would be better if any occuptaions salary went down
      .interpolate(d3.interpolateHcl)
      .range([
        // d3.rgb('#e1645d'), //low red
        d3.rgb('#b2233a'), //low dark red
        d3.rgb('#53b27c'), //high green
      ]);

    updateData(0);

    function updateData(speed) {
      // MAIN_CHART_LOCATION
      var filtered_data = data_by_occupation_arr.map((occ) => {
        return Object.assign({},
          occ, {
            rows: occ.rows.filter((row) => row.Location == MAIN_CHART_LOCATION),
          }
        );
      })
      filtered_data = filtered_data.filter((occ) => {
        return occ.rows.length > 0
      })

      var occupation = svg.selectAll('.occupations')
        .data(filtered_data, d => d['Indicator Name']);
      // accessing 'indicator name' in case of data was sorted differently

      occupation.exit().remove();

      var enter = occupation
        .enter()
        .append('g')
        .attr('class', 'occupations')
      exp.enter = enter;

      function linechart_mouseclick(d){
        var name = d3.select(this).attr('name');
        var occ = data_by_occupation_arr.find(r => r.name == name)
        exp.selected_occ = name;
        sidepanel.open(occ, data_by_occupation_arr, exp.onSidePanelClose);
        setTimeout(() => {
          // redraw it squished
          exp.chart();
        }, 50);
      }

      function linechart_mouseover(matching){
        var name = d3.select(this).attr('name');

        // highlight this line, fade other lines
        enter.selectAll('.line').classed('line--hover', (d, i) => {
          return d.name == name;
        }).classed('line--fade', (d, i) => {
          return d.name !== name;
        });
        var group = enter.select('.subgroup-container[name="'+name+'"] .tooltip')
          .style('opacity', '1')
        group = enter.select('.subgroup-container[name="'+name+'"] .tooltip_y_first')
          .style('opacity', '1')
        group = enter.select('.subgroup-container[name="'+name+'"] .tooltip_y_last')
          .style('opacity', '1')

        d3
          .selectAll('.chart_occupation_trends .chart_main .subgroup-container')
          .classed('hover', (d, i) => {
            return d.name == name;
          }).classed('fade', (d, i) => {
            return d.name !== name;
          })

      }
      function linechart_mouseleave(){
        var enter = exp.enter;
        // turn off hover and fade effects
        if(sidepanel.is_open){
          exp.toggleGrayedOutMainChart(true);
        }else{
          enter.selectAll('.line')
            .classed('line--hover', false)
            .classed('line--fade', false);

          d3
            .selectAll('.chart_occupation_trends .chart_main .subgroup-container')
            .classed('hover', (d, i) => {
              return false;
            }).classed('fade', (d, i) => {
              return false;
            })

        }

        var group = enter.select('.subgroup-container .tooltip')
          .style('opacity', '0')
        group = enter.select('.subgroup-container .tooltip_y_first')
          .style('opacity', '0')
        group = enter.select('.subgroup-container .tooltip_y_last')
          .style('opacity', '0')


      }

      var subgroup_c = enter
        .append('g')
        .attr('class', 'subgroup-container')
        .attr('name', d => d.name)
        .attr('transform', d => {
          var latest = d.latest.Value;
          var y = height-scaleLastYearValue(latest);
          return 'translate(0, '+y+')'
        })

      /*
      filtered_data.forEach((occ, i) => {
        var linearGradient = svg.append("defs")
            .append("linearGradient")
            .attr("id", "linear-gradient-"+i)

        var color_rotate = false;
        if(rotate_axis && color_rotate){
          linearGradient.append("stop")
              .attr("offset", "0%")
              .attr("stop-color", color(occ.totalChangeDiff));

          linearGradient.append("stop")
              .attr("offset", "100%")
              .attr("stop-color", 'white');
        }else{
          linearGradient.append("stop")
              .attr("offset", "0%")
              .attr("stop-color", 'white');

          linearGradient.append("stop")
              .attr("offset", "100%")
              .attr("stop-color", color(occ.totalChangeDiff));
        }
      });
      */

      subgroup_c
        .append('circle')
        .attr('name', d => d.name)
        .attr('class', 'end_circle')
        .attr('fill', (d, i) => color(d.totalChangeDiff))
        .attr('stroke', (d, i) => color(d.totalChangeDiff))
        .attr('r', 3)
        .attr('cx', (d, i) => {
          var di = util.last(d.rows)
          return xLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, 8, di.Value) + 1
        })
        .attr('cy', (d, i) => {
          var di = util.last(d.rows)
          // return yLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, 8, di.Value)
          return - smallYScale(d.latest.Value - di.Value);
        });


      subgroup_c
        .append('path')
        .attr('name', d => d.name)
        .attr('class', 'line occupations_subgroup')
        // .attr('stroke', (d, i) => 'url(#linear-gradient-'+i+')')
        .attr('stroke', (d, i) => color(d.totalChangeDiff))
        .on('mouseover', linechart_mouseover )
        .on('mouseleave', linechart_mouseleave )
        .on('click', linechart_mouseclick )

        .attr('d', function(d){
          return d3.line()
            .curve(d3.curveCardinal)
            .x(function(di, i) {
              return xLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, i, di.Value)
            })
            .y(function(di, i) {
              // return yLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, i, di.Value)
              return - smallYScale(d.latest.Value - di.Value);
            })(d.rows)
        })

      subgroup_c
        .append('g')
        .attr('class', 'tooltip_y_first smaller')
        .style('opacity', '0')
        .attr('transform', (d, i) => {
          var di = util.last(d.rows)
          var _y = - smallYScale(d.latest.Value - d.lowestValue);
          // var _y = yLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, i, d.lowestValue);
          return 'translate(' + xLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, 0, di.Value) + ','
          + _y + ')'
        })
        .html(d => {
          return ''
            +'<text text-anchor="end" >'+d.earliest.Year+'</text>'
            +'<text style="font-weight: normal" text-anchor="end" transform="translate(0, 15)">'+util.formatPrice(d.earliest.Value, true)+'</text>'
        })


      subgroup_c
        .append('g')
        .attr('class', 'tooltip_y_last smaller')
        .style('opacity', '0')
        .attr('text-anchor', 'start')
        .attr('transform', (d, i) => {
          var di = util.last(d.rows)
          var _y = - smallYScale(d.latest.Value - d.lowestValue);
          // var _y = yLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, i, d.lowestValue);
          return 'translate('
          + (xLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, year_idx_last, di.Value)+8)
          + ','
          + _y + ')'
        })
        .html(d => {
          return ''
            +'<text text-anchor="start">'+d.latest.Year+'</text>'
            +'<text style="font-weight: normal" text-anchor="start" transform="translate(0, 15)">'+util.formatPrice(d.latest.Value, true)+'</text>'
        })

      subgroup_c
        .append('g')
        .attr('transform', (d, i) => {
          if(d.rows.length){
            var di = util.last(d.rows)
            var _y = - smallYScale(d.latest.Value - d.lowestValue) + 35;
            // var _y = (yLineCombined({totalChangeDiff: d.totalChangeDiff, earliest: d.earliest.Value, latest: d.latest.Value}, i, d.lowestValue)+35);
            return 'translate(' + xLineCombined({totalChangeDiff: d.totalChangeDiff,  earliest: d.earliest.Value, latest: d.latest.Value}, year_idx_half, di.Value) + ','
            + _y + ')'
          }else{
            return 'translate(-999999,-99999999)'
          }
        })
        .attr('class', 'tooltip')
        .style('opacity', '0')
        .html(d => '<text text-anchor="middle">'+d.name+'</text>' )

      occupation = occupation.merge(enter)

    }

    var selectbox = d3.select('#locationSelect')
      .on('change', function() {
        MAIN_CHART_LOCATION = this.value;
        updateData(CHART_UPDATE_SPEED);
      })


    if(sidepanel.is_open){
      exp.toggleGrayedOutMainChart(true);
    }


    function main_axis(_) {
      var svg = d3.select('.chart_occupation_trends .chart_main svg')
      var defs = svg.append('svg:defs')
      defs
        .append('svg:marker')
        .attr('id', 'triangle_black')
        .attr('refX', 3)
        .attr('refY', 3)
        .attr('markerWidth', 6)
        .attr('markerHeight', 6)
        .attr('orient', 'auto')
        .append('path')
        .attr('d', 'M 0 1 4 3 0 5')
        .style('fill', 'black');

      var g = svg
        .append('g')
        .attr('class', 'axis_x')
      if(rotate_axis){
        g.attr('transform', _ => {
          return 'translate('+30+', '+((height/2)+ 30)+'), rotate(270)'
        })
      }else{
        g.attr('transform', _ => {
          return 'translate('+(width/2)+', '+height+')'
        })
      }

      g.append('text')
        .text('2018 Median Earning')
        .style('text-anchor', 'middle')
        .style('font-weight', 'bold')

      var mid_text_w = 130;
      g.append('text')
        .text('decreasing')
        .style('text-anchor', 'middle')
        .style('font-weight', 'bold')
        .style('opacity', '0.7')
        .attr('x', -mid_text_w)

      g.append('text')
        .text('increasing')
        .style('text-anchor', 'middle')
        .style('font-weight', 'bold')
        .style('opacity', '0.7')
        .attr('x', mid_text_w)

      var arrow_y_off = -5;

      //cross lines
      g.append('line')
        .attr('x1', -(mid_text_w+45))
        .attr('x2', -(mid_text_w+45+40))
        .attr('y1', arrow_y_off)
        .attr('y2', arrow_y_off)
        .attr('stroke-width', 2)
        .attr('stroke', 'black')
        .attr('marker-end', 'url(#triangle_black)');

      g.append('line')
        .attr('x1', (mid_text_w+45))
        .attr('x2', (mid_text_w+45+40))
        .attr('y1', arrow_y_off)
        .attr('y2', arrow_y_off)
        .attr('stroke-width', 2)
        .attr('stroke', 'black')
        .attr('marker-end', 'url(#triangle_black)');

      // Y axis
      g = svg
        .append('g')
        .attr('class', 'axis_y')
      if(rotate_axis){
        g.attr('transform', _ => {
          return 'translate('+(width/2+120)+', '+(height_full - 20)+')'
        })
      }else{
        g.attr('transform', _ => {
          return 'translate('+30+', '+((height/2)+ 30)+'), rotate(270)'
        })
      }

      g.append('text')
        .text('Change in earning')
        .style('text-anchor', 'middle')
        .style('font-weight', 'bold')

      mid_text_w = 110;
      g.append('text')
        .text('decreasing')
        .style('text-anchor', 'middle')
        .style('font-weight', 'bold')
        .style('opacity', '0.7')
        .attr('x', -mid_text_w)

      g.append('text')
        .text('increasing')
        .style('text-anchor', 'middle')
        .style('font-weight', 'bold')
        .style('opacity', '0.7')
        .attr('x', mid_text_w)

      //cross lines
      g.append('line')
        .attr('x1', mid_text_w+45)
        .attr('x2', mid_text_w+45+40)
        .attr('y1', arrow_y_off)
        .attr('y2', arrow_y_off)
        .attr('stroke-width', 2)
        .attr('stroke', 'black')
        .attr('marker-end', 'url(#triangle_black)');

      g.append('line')
        .attr('x1', -(mid_text_w+45))
        .attr('x2', -(mid_text_w+45+40))
        .attr('y1', arrow_y_off)
        .attr('y2', arrow_y_off)
        .attr('stroke-width', 2)
        .attr('stroke', 'black')
        .attr('marker-end', 'url(#triangle_black)');


      var orig_range_y = scaleLastYearValue.range();
      var orig_domain_y = scaleLastYearValue.domain();
      var scaleYAxisLeft = scaleLastYearValue
        .copy()
        .domain([
          orig_domain_y[0] + 1800,
          orig_domain_y[1] - 4000,
        ])
        .rangeRound([orig_range_y[1] + 110, orig_range_y[0]+190])

      var y_axis2 = d3.axisLeft()
        .scale(scaleYAxisLeft)
        .tickFormat(function(d) {
          // var s = formatNumber(d / 1e6);
          var s = '$'+util.formatPrice(d);
          return s

        });

      svg.append('g')
        .attr('class', 'tick_axis_left')
        .call(y_axis2)
        .attr('transform', 'translate(90, 0)');


      // copy of x main scale + bit bigger range to include OCCUPATION_LINECHART_WIDTH
      var orig_range = scaleOccupationChangePct.range();
      var scaleOccupationChangePct_for_axis = scaleOccupationChangePct
        .copy()
        .rangeRound([orig_range[0], orig_range[1]+OCCUPATION_LINECHART_WIDTH+10])
      var x_axis2 = d3.axisBottom()
        // .scale(scaleOccupationChangePct);
        .scale(scaleOccupationChangePct_for_axis)
        .tickFormat(function(d) {
          return d + '%';
        });

      svg.append('g')
        .attr('class', 'tick_axis_bot')
        .call(x_axis2)
        .attr('transform', 'translate('+(0)+', '+(height+55)+')')

    }
    main_axis();
  },

  setupSearch: function(occ_keys, data_by_occupation_arr){

    function highlightMatching(matching){
      var enter = exp.enter;

      // highlight this line, fade other lines
      enter.selectAll('.line').classed('line--searchhover', (d, i) => {
        return matching.filter((m) => m.name == d.name).length > 0;
      }).classed('line--searchfade', (d, i) => {
        return matching.filter((m) => m.name == d.name).length == 0;
      });

      d3
        .selectAll('.chart_occupation_trends .chart_main .subgroup-container')
        .classed('searchhover', (d, i) => {
          return matching.filter((m) => m.name == d.name).length > 0;
        }).classed('searchfade', (d, i) => {
          return matching.filter((m) => m.name == d.name).length == 0;
        })
    }
    function removeColorization(){
      var enter = exp.enter;
      // turn off hover and fade effects
      enter.selectAll('.line')
        .classed('line--searchhover', false)
        .classed('line--searchfade', false);

      d3
        .selectAll('.chart_occupation_trends .chart_main .subgroup-container')
        .classed('searchhover', false)
        .classed('searchfade', false)
    }

    function handleChange(value){
      var val = value;
      if(!value){
        val = this.value || '';
      }
      var matching = data_by_occupation_arr.filter((d) => {
        return util.strsearch(d.name, val);
      })
      if(val.trim().length == 0){
        removeColorization();
      }else{
        highlightMatching(matching);
      }
    }

    exp.onSidePanelClose = function(){
      // rehighlight based on search
      handleChange(d3.select('#inputbox').property('value') || '');
      exp.selected_occ = null;
      exp.chart();
    }

    var inputBox = d3.select('#inputbox')
      .on('change', handleChange)
      .on('keydown', function(){ var a = arguments; setTimeout(() => {handleChange.apply(this, arguments)}, 5) })
  },
  onSidePanelClose: function(){
    // 'this func should be overriden'
    exp.selected_occ = null;
    exp.chart();
  },
  toggleGrayedOutMainChart: (grayed) => {
    var selected = exp.selected_occ;
    if(grayed){
      d3
        .selectAll('.chart_occupation_trends .chart_main .subgroup-container')
        .classed('hover', (d, i) => {
          return d.name == selected;
        }).classed('fade', (d, i) => {
          return d.name !== selected;
        }).classed('searchfade', (d, i) => {
          return false;
        }).classed('searchhover', (d, i) => {
          return false;
        });

      d3.selectAll('.chart_occupation_trends .chart_main .line').classed('line--hover', (d, i) => {
        return d.name == selected;
      }).classed('line--fade', (d, i) => {
        return d.name !== selected;
      }).classed('line--searchfade', (d, i) => {
        return false;
      }).classed('line--searchhover', (d, i) => {
        return false;
      })

    }else{
      d3.selectAll('.chart_occupation_trends .chart_main .line').classed('line--hover', (d, i) => {
        return false;
      }).classed('line--fade', (d, i) => {
        return false;
      }).classed('line--searchfade', (d, i) => {
        return false;
      }).classed('line--searchhover', (d, i) => {
        return false;
      });
    }
  },

}

module.exports = exp;
