var util = require('./util.js');
var _ = require('lodash');
var Data = require('../src/data_access.js');
var cmp_cross = require('./cmp_cross.js');
var sidepanel = require('./chart_population_earnings_map_sidepanel.js');

var exp = {
  init: function(){
    var page_template = require('../templates/chart_pop_earnings_map.html');
    d3.select('.page').html(page_template);
    sidepanel.is_open = false;
    Data.get_occupations_wages((data_wages) => {
      Data.people_count_by_occupation((data_people_count) => {
        setTimeout(() => {
          exp.chart(data_wages, data_people_count);
        }, 50);
      })
    })
  },

  chart: (data_all_occupations, people_count_by_occupation) => {
    d3.select('.legend_item .loc-label--city').text(util.loc_city_name);
    d3.select('.legend_item .loc-label--state').text(util.loc_state_name);
    d3.select('.legend_item .loc-label--us').text(util.loc_us_name);

    var prepped = Data.occupations_wages_prepped(data_all_occupations);
    var data_by_occupation_arr = prepped.data_by_occupation_arr;

    var groups = [];
    data_by_occupation_arr.forEach((occ) => {
      var people_count_by_occupation_filtered = people_count_by_occupation.filter(
        (r) => {
          return (r['Indicator Name']).toLowerCase() == occ.name.toLowerCase();
        })

      var xDomain = Data.people_count_domain(
        people_count_by_occupation_filtered, people_count_by_occupation,
        data_by_occupation_arr, occ.name
      );

      var yDomain = Data.salary_domain(
        data_by_occupation_arr, occ.name
      );

      groups.push({
        label: occ.name,
        xDomain: xDomain,
        yDomain: yDomain,
        data_occ_emp_count: people_count_by_occupation_filtered,
        data_occ: occ,
      })
    });

    groups = exp.groups = _.sortBy(groups, 'label')

    // generate subchart containers
    var subcharts = d3.select('.subcharts');
    groups.forEach((group, i) => {

      var selector;
      var is_all_occ = false;
      if(group.label.toLowerCase() == 'all occupations'){
        selector = '.chart_main ';
        is_all_occ = true;

        d3.select(selector)
          .on('click', () => {
            sidepanel.open(group, exp.onclose)
          });
      }else{
        selector = '.subcharts .subchart--'+i+'';
        subcharts
          .append('div')
          .attr('class', 'subchart subchart--'+i)
          .html('<div class="subchart__inner"><h3>'+group.label+'</h3><svg></svg></div>')
          .on('click', () => {
            sidepanel.open(group, exp.onclose)
          });
      }
      group.lines = [
        {
          name: util.loc_city_name,
          location: util.loc_city,
          color: '#1d9db9',
        },
        {
          name: util.loc_state_name,
          location: util.loc_state,
          color: '#ffd966',
        },
        {
          name: util.loc_us_name,
          location: util.loc_us,
          color: '#a6a6a6',
        }
      ];



      var margin = {top: 20, right: 70, bottom: 70, left: 20},
        height_full = 320,
        width_full = 320;
      if(is_all_occ){
        height_full = 280;
        margin.bottom = 20;
      }
      var width = width_full - margin.left - margin.right,
        height = height_full - margin.top - margin.bottom;
      var header_h = 55;

      if(!is_all_occ){
        margin = {top: 5, right: 20, bottom: 20, left: 20};
        var containerBox = d3.select(selector)
            .node().getBoundingClientRect(); //for svg use .getBBox();
        width_full = containerBox.width;
        height_full = Math.min(containerBox.width, containerBox.height-header_h);
        if(height_full < width_full){
          width_full = height_full;
        }
        width = width_full - margin.left - margin.right;
        height = height_full - margin.top - margin.bottom;
      }

      var xCrossArrow = d3.scaleLinear()
          .range([0, width*0.8])
          .domain(group.xDomain)

      var yCrossArrow = d3.scaleLinear()
          .domain(group.yDomain)
          .range([height*0.8, 0])


      group.lines.forEach((line, i) => {
        line.x1 = width / 2;
        line.y1 = height / 2;

        // Y2
        // ---------------------------------------------------------------------
        var val_salary_2010 = _.find(group.data_occ.rows, {Location: line.location, Year: 2010})
        var val_salary_2018 = _.find(group.data_occ.rows, {Location: line.location, Year: 2018})


        var salary_increase_pct = 0;
        if(val_salary_2010 && val_salary_2018){
          var p = (v) => parseFloat(v, 10);
          salary_increase_pct = util.percentChange(p(val_salary_2010.Value), p(val_salary_2018.Value));
        }

        line.y2_pct = salary_increase_pct;
        line.y2 = yCrossArrow( salary_increase_pct ) + (height*0.1);


        // X2
        // ---------------------------------------------------------------------
        var people_with_that_occupation_2010 = getEmpCt(null, 2010, line.location, false);
        var people_with_that_occupation_2018 = getEmpCt(null, 2018, line.location, false);

        if(people_with_that_occupation_2018.length == 0 || people_with_that_occupation_2010.length == 0){
          console.warn('people_with_that_occupation.length == 0');
          return xCrossArrow(0) + (width*0.1)
        }
        var emp_increase_pct = util.percentChange(people_with_that_occupation_2010[0].Value, people_with_that_occupation_2018[0].Value)

        line.x2_pct = emp_increase_pct;
        line.x2 =  xCrossArrow(emp_increase_pct) + (width*0.1)
      });

      function getEmpCt(src, yr, loc, allOccupations){
        if(!src){
          src = group.data_occ_emp_count;
          if(allOccupations == true){
            src = people_count_by_occupation;
          }
        }
        return src.filter((r)=>{
          return r.Location == loc && r.Year == yr
        })
      }

      var draw_labels = is_all_occ;
      cmp_cross.chart(selector+' svg', group.lines, margin, width_full, height_full, draw_labels);
    });


  },

  onclose: () => {
  }
}


module.exports = exp;
