var util = require('./util.js');
var Data = require('../src/data_access.js');
var _ = require('lodash');
var cmp_cross = require('./cmp_cross.js');
var cmp_tables = require('./cmp_tables.js');

var curveType = d3.curveCatmullRom;
var people_count_by_occupation = null;

var parseTime = d3.timeParse('%Y');

var exp = {
  is_open: false,

  selected_occ: null,

  open: (data, data_all_occ, onclose) => {
    exp.onclose = onclose;
    exp.selected_occ = data;

    var sidepanel = d3.selectAll('.sidepanel')
      .classed('open', true)
      .select('h2')
      .text(data.name)

    d3.selectAll('.sidepanel .close')
      .on('click', exp.close)

    exp.chart(data);

    Data.people_count_by_occupation((count_data) => {
      people_count_by_occupation = count_data;
      exp.cross_chart(data, people_count_by_occupation, data_all_occ);
      setTimeout(() => {
        cmp_tables.sidepanel_tables(data, people_count_by_occupation);
      }, 100);
    })
    exp.is_open = true;
  },

  close: () => {
    d3.select('.sidepanel')
      .classed('open', false)
    exp.is_open = false;
    if(exp.onclose) {
      exp.onclose();
    }
  },

  dataByLocation: (row, loc) => {
    var newRows = row.rows.filter((row) => row.Location == loc)
    var latest = util.last(newRows);
    var earliest = newRows[0];
    return Object.assign({}, row,
      {
        rows: newRows,
        latest: latest,
        earliest: earliest,
      }
    );
  },

  chart: (data) => {
    var width_guide = d3.select('.sidepanel_chart1').node().getBoundingClientRect();

    var margin = {top: 30, right: 50, bottom: 70, left: 20},
      width_full  = width_guide.width,
      width = width_full - margin.left - margin.right,
      height = 340 - margin.top - margin.bottom;


    var data_us = exp.dataByLocation(data, util.loc_us);
    var data_az = exp.dataByLocation(data, util.loc_state);
    var data_bc = exp.dataByLocation(data, util.loc_city);

    // set the ranges
    var x = d3.scaleTime()
        .range([0, width])

    var y = d3.scaleLinear()
        .range([height, 0]);

    var svg = d3
      .select('.sidepanel .sidepanel_chart1 svg')
        .attr('width', width_full)
        .attr('height', height + margin.top + margin.bottom)
      .html('')
      .append('g')
        .attr('transform',
              'translate(' + margin.left + ',' + margin.top + ')');

    // Scale the range of the data
    x.domain(d3.extent(data.rows, (d) => parseTime(''+d.Year) ));
    var y_values = d3.extent(
      _.map(data.rows, (d) => d.Value).concat([0])
    )
    y.domain(y_values);

    svg.append('path')
      .datum(data_us.rows)
      .attr('name', 'data_us')
      .attr('fill', 'none')
      .attr('stroke', util.color_sidepanel_focus_secondary)
      .style('stroke-dasharray', ('9, 9'))
      .attr('stroke-width', 1.5)
      .attr('d', d3.line()
        .curve(curveType)
        .x(function(d) { return x((parseTime(''+d.Year))) })
        .y(function(d) { return y(d.Value) })
      )

    svg.append('path')
      .datum(data_az.rows)
      .attr('name', 'data_az')
      .attr('fill', 'none')
      .attr('stroke', util.color_sidepanel_focus_secondary) // gray
      .attr('stroke-width', 1.5)
      .style('stroke-dasharray', ('3, 3'))
      .attr('d', d3.line()
        .curve(curveType)
        .x(function(d) { return x((parseTime(''+d.Year))) })
        .y(function(d) { return y(d.Value) })
      )

    svg.append('path')
      .datum(data_bc.rows)
      .attr('name', 'data_bc')
      .attr('fill', 'none')
      .attr('stroke', util.color_sidepanel_green )
      .attr('stroke-width', 3)
      .attr('d', d3.line()
        .curve(curveType)
        .x(function(d) { return x((parseTime(''+d.Year))) })
        .y(function(d) { return y(d.Value) })
      )

    // bullhead area fill
    var area = d3.area()
        .curve(curveType)
      .x(function(d) { return x((parseTime(''+d.Year))) })
      .y1(function(d) { return y(d.Value) })
      .y0(height)

    svg.append('path')
      .datum(data_bc.rows)
      .attr('class', 'line_area')
      .attr('d', area);

    // Create the circle that travels along the curve of chart
    var groups = [
      { name: util.loc_city_name, data: data_bc },
      { name: util.loc_state_name, data: data_az },
      { name: util.loc_us_name, data: data_us },
    ];

    groups.forEach((group, i) => {
      group.focus = svg
        .append('g')
        .append('circle')
          .attr('stroke',
            group.name == util.loc_city ?
            util.color_sidepanel_green: util.color_sidepanel_focus_secondary
          )
          .attr('r', 4.5)
          .attr('fill', 'white')
          .style('opacity', 0)

      // Create the text that travels along the curve of chart
      group.labelText = svg
        .append('g')
        .style('opacity', 0);

      var label_x_offset = 170;
      var label_y_offset = height + 30;
      group.labelText
        .append('text')
          .attr('x', 100*i + label_x_offset)
          .attr('y', 20 + label_y_offset)
          .attr('class', 'label_name')
          .attr('text-anchor', 'middle')
          .attr('alignment-baseline', 'middle')
          .attr('fill',
            group.name == util.loc_city ?
            util.color_sidepanel_green: util.color_sidepanel_focus_secondary_light
          )
          .html(group.name);
      ;

      group.labelText
        .append('text')
          .attr('x', 100*i + label_x_offset)
          .attr('y', label_y_offset)
          .attr('class', 'label_value')
          .attr('text-anchor', 'middle')
          .attr('alignment-baseline', 'middle')
          .attr('fill',
            group.name == util.loc_city ?
            util.color_sidepanel_green: util.color_sidepanel_focus_secondary_light
          )
          .html(0);
      ;
    })

    // -------------------------------------------------------------------------
    // X axis mouseover label

    function initMouseOverYear(){
      var hovered_year = 2011;

      var yearTooltip = svg.append('g')
        .attr('class', 'yearTooltip')
        .style('opacity', 0)
        .attr('transform', 'translate('+ x(parseTime(hovered_year)) +',0)');

      yearTooltip
        .append('text')
        .attr('class', 'year_text')
        .attr('y', height + 15)
        .attr('text-anchor', 'middle')
        .style('font-size', '10px')
        .style('font-weight', '400')
        .style('font-family', 'sans-serif')
        .style('color', '#212529')
        .text('2011')


      yearTooltip
        .append('path')
        .attr('stroke', util.color_sidepanel_focus_secondary)
        .style('stroke-dasharray', ('1, 1'))
        .attr('stroke-width', 1.5)
        .attr('stroke-width', 1.5)
        .attr('d', () => {
          var pos_1 = [0, 0];
          var pos_2 = [0, height];
          return d3.line()([pos_1, pos_2])
        })
    }

    function drawMouseOverYearLabel(year_i){
      var hovered_year = 2010+year_i;
      var yearTooltip = svg.select('.yearTooltip')
        .attr('transform', 'translate('+ x(parseTime(hovered_year)) +',0)');
      if(year_i == 0){
        svg.select('.yearTooltip').style('opacity', 0);
        return
      }else{
        svg.select('.yearTooltip').style('opacity', 1);
      }

      if(hovered_year > 2010 && hovered_year < 2018){
        yearTooltip.select('.year_text')
          .attr('opacity', 1)
          .text(hovered_year)
      }else{
        yearTooltip.select('.year_text')
          .attr('opacity', 0)
          .text(hovered_year)
      }

    }

    initMouseOverYear();


    // -------------------------------------------------------------------------
    // Add the X Axis
    svg.append('g')
      .attr('name', 'axis_bottomx')
      .attr('transform', 'translate(0,' + height + ')')
      .call(d3.axisBottom(x).tickValues([parseTime(2010), parseTime(2018)]));

    // Add the Y Axis
    svg.append('g')
      .attr('name', 'axis_lefty')
      .call(d3.axisLeft(y).ticks(0));

    // Create a rect on top of the svg area: this rectangle recovers mouse position
    svg
      .append('rect')
      .style('fill', 'none')
      .style('pointer-events', 'all')
      .attr('width', width)
      .attr('height', height)
      .on('mouseover', mouseover)
      .on('mousemove', mousemove)
      .on('mouseout', mouseout);


    // line labels on the right
    // -------------------------------------------------------------------------

    groups.forEach((group, i) => {
      var isCity = group.name == util.loc_city;
      group.targetY = group.y = y(group.data.latest.Value) - (isCity ? 5: 5)
      group.fx = 0;
    });

    // Define a custom force
    const forceClamp = (min, max) => {
      let nodes;
      const force = () => {
        nodes.forEach(n => {
          if (n.y > max) n.y = max;
          if (n.y < min) n.y = min;
        });
      };
      force.initialize = (_) => {
        nodes = _
      };
      return force;
    }
    var labelHeight = 20;
    // Set up the force simulation
    const force = d3.forceSimulation()
      .nodes(groups)
      .force('collide', d3.forceCollide(labelHeight / 2))
      .force('y', d3.forceY(d => d.targetY).strength(1))
      .force('clamp', forceClamp(-15, height))
      .stop();

    // Execute the simulation
    for (let i = 0; i < 300; i++) force.tick();

    groups.forEach((group, i) => {

      var isCity = group.name == util.loc_city;
      group.x = x((parseTime(''+2018))) + (isCity ? 0: 5)
      svg
        .append('text')
        .attr('text-anchor',
          isCity ? 'middle' : 'start'
        )
        .style('pointer-events', 'none')
        .style('fill', isCity ?
          util.color_sidepanel_green : util.color_sidepanel_focus_secondary_light
        )
        .attr('x', group.x )
        .attr('y', group.y )
        .html(group.name)
    });

    function mouseover(){
      svg.select('.yearTooltip').style('opacity', 1);
      groups.forEach((group) => {
        group.focus.style('opacity', 1)
        group.labelText.style('opacity', 1)
      });
    }

    var bisect = d3.bisector(function(d) { return parseTime(d.Year); }).right;

    function mousemove(){
      // recover coordinate we need
      var mousex = d3.mouse(this)[0] - (x(parseTime(2011))/2)
      var x0 = x.invert(mousex);
      var i = 0;
      groups.forEach((group) => {
        i = bisect(group.data.rows, x0, 0);
        var selectedData = group.data.rows[i];

        if(!selectedData){
          return;
        }
        group.focus
          .attr('cx', x(parseTime(selectedData.Year)))
          .attr('cy', y(selectedData.Value))
        group.labelText.selectAll('.label_value')
          .html(util.formatPrice(selectedData.Value, true));
      });
      drawMouseOverYearLabel(i);
    }

    function mouseout(){
      svg.select('.yearTooltip').style('opacity', 0);
      groups.forEach((group) => {
        group.focus.style('opacity', 0)
        group.labelText.style('opacity', 0)
      });
    }

  },

  cross_chart: (data_of_occupation, people_count_by_occupation, data_all_occupations) => {

    var people_count_by_occupation_filtered = people_count_by_occupation.filter(
      (r) => {
        return r['Indicator Name'] == data_of_occupation.name
      })

    var margin = {top: 20, right: 70, bottom: 30, left: 10},
      height_full = 320,
      width_full = 320,
      width = width_full - margin.left - margin.right,
      height = height_full - margin.top - margin.bottom;

    var xDomain = Data.people_count_domain(
      people_count_by_occupation_filtered, people_count_by_occupation,
      data_all_occupations, data_of_occupation.name
    );

    var yDomain = Data.salary_domain(
      data_all_occupations, data_of_occupation.name
    );

    // 'number of people with that occupation'
    var xCrossArrow = d3.scaleLinear()
        .range([0, width*0.8])
        .domain(xDomain)

    var yCrossArrow = d3.scaleLinear()
        .range([height*0.8, 0])
        .domain(yDomain);

    var lines = [
      {
        name: util.loc_city_name,
        location: util.loc_city,
        color: '#1d9db9',
      },
      {
        name: util.loc_state_name,
        location: util.loc_state,
        color: '#ffd966',
      },
      {
        name: util.loc_us_name,
        location: util.loc_us,
        color: '#a6a6a6',
      }
    ]

    lines.forEach((line, i) => {
      line.x1 = width / 2;
      line.y1 = height / 2;

      function getX2(){
        var people_with_that_occupation_2010 = 0;
        var people_with_that_occupation_2010_rows = getEmpCt(null, 2010, line.location, false);
        if(people_with_that_occupation_2010_rows.length){
          people_with_that_occupation_2010 = people_with_that_occupation_2010_rows[0].Value;
        }
        var people_with_that_occupation_2018 = 0;
        var people_with_that_occupation_2018_rows = getEmpCt(null, 2018, line.location, false);
        if(people_with_that_occupation_2018_rows.length){
          people_with_that_occupation_2018 = people_with_that_occupation_2018_rows[0].Value;
        }

        if(people_with_that_occupation_2018.length == 0){
          console.warn('people_with_that_occupation.length == 0');
          return xCrossArrow(0) + (width*0.1)
        }
        var emp_increase_pct = util.percentChange(people_with_that_occupation_2010, people_with_that_occupation_2018)
        return xCrossArrow(emp_increase_pct) + (width*0.1)
      }

      function getY2(){
        var salary_2010 = data_of_occupation.rows.filter((d) => d.Year == 2010 && d.Location == line.location).map((d) => d.Value);
        var salary_2018 = data_of_occupation.rows.filter((d) => d.Year == 2018 && d.Location == line.location).map((d) => d.Value);
        var increase_pct = util.percentChange(salary_2010, salary_2018);
        return yCrossArrow( increase_pct ) + (height*0.1)
      }

      line.x2 = getX2();
      line.y2 = getY2();

      function getEmpCt(src, yr, loc, allOccupations){
        if(!src){
          src = people_count_by_occupation_filtered;
          if(allOccupations == true){
            src = people_count_by_occupation;
          }
        }
        return src.filter((r)=>{
          return r.Location == loc && r.Year == yr
        })
      }
    });

    var svgSelector = '.sidepanel .cross_chart svg'
    cmp_cross.chart(svgSelector, lines, margin, width_full, height_full, true)


    // update labels
    var crossParent = d3.select('.sidepanel .cross_chart')
    crossParent.select('.legend_item .loc-label--city').text(util.loc_city_name);
    crossParent.select('.legend_item .loc-label--state').text(util.loc_state_name);
    crossParent.select('.legend_item .loc-label--us').text(util.loc_us_name);
    // -------
  },

}

module.exports = exp;
