var d3 = require('d3');
var _ = require('lodash');
var util = require('../src/util.js');

var cache = {
  occupations_wages: null,
  people_count_by_occupation: null,
  genders_data: null,
}

function mapCategory(category, subcategory, group){
  if(category !== subcategory && subcategory == 'All' && group == 'All'){
    return category;
  }
  if(category !== subcategory && group == 'All'){
    return subcategory;
  }
  if(category !== subcategory && subcategory !== group){
    return group;
  }
  return category;
}

function normalizeUSName(value){
  if(value.toLowerCase() == 'united states') {
    return 'US';
  }
  return value;
}
function normalizeStateName(value){
  var allowed = window.location_state_names.map((x) => x.toLowerCase());
  if(allowed.indexOf(value.toLowerCase()) > -1) {
    return window.location_state_names[0];
  }
  return value;
}

var exp = {
  init: () => {
    var load_data_folder = window.load_data_folder;

    exp.url_prefix = '/data/'+load_data_folder;

    exp.data_json_format = window.data_json_format;
    if(window.data_json_format){
      exp.data_ext = 'json';
    }else{
      exp.data_ext = 'csv';
    }
    exp.fetcher = exp.data_json_format ? d3.json : d3.csv;
  },

  get_occupations_wages: function (cb){
    if(cache.occupations_wages){
      setTimeout(() => {
        cb(cache.occupations_wages);
      }, 50)
    }else{
      exp.fetcher(exp.url_prefix+'/occupations-wages.'+exp.data_ext, (d) => {
        if(exp.data_json_format){
          d = exp.toCSVLikeOccupationsWages(d)
        }
        cache.occupations_wages = d;
        cb(cache.occupations_wages);
      });
    }
  },

  occupations_wages_prepped: (raw_data) => {
    var MAIN_CHART_LOCATION = util.loc_city;

    var data_by_occupation = {};
    var lowestLastYearValue = util.MAX_SAFE_INTEGER;
    var highestLastYearValue = util.MIN_SAFE_INTEGER;
    var lowestDiffValue = util.MAX_SAFE_INTEGER;
    var highestDiffValue = util.MIN_SAFE_INTEGER;
    var lowestOverallValue = util.MAX_SAFE_INTEGER;
    var highestOverallValue = util.MIN_SAFE_INTEGER;
    var lowestYear = util.MAX_SAFE_INTEGER;
    var highestYear = util.MIN_SAFE_INTEGER;

    raw_data.forEach((row) => {
      var occupation = row['Indicator Name'];
      if(data_by_occupation[occupation] == undefined){

        data_by_occupation[occupation] = {
          name: row['Indicator Name'],
          rows: [],
          earliest: null,
          latest: null,
          lowestValue: util.MAX_SAFE_INTEGER,
          totalChangeDiff: 0,
        };
      }
      row.Year = parseFloat(row.Year, 10);

      lowestYear = Math.min(lowestYear, row.Year);
      highestYear = Math.max(highestYear, row.Year);

      row.Value = parseFloat(row.Value, 10);
      data_by_occupation[occupation].rows.push(row);

      if(!data_by_occupation[occupation].earliest && row.Location == MAIN_CHART_LOCATION) {
        data_by_occupation[occupation].earliest = row;
      }
      if(!data_by_occupation[occupation].latest && row.Location == MAIN_CHART_LOCATION) {
        data_by_occupation[occupation].latest = row;
      }
      if(data_by_occupation[occupation].earliest && data_by_occupation[occupation].earliest.Year > row.Year && row.Location == MAIN_CHART_LOCATION){
        data_by_occupation[occupation].earliest = row;
      }
      if(data_by_occupation[occupation].latest && data_by_occupation[occupation].latest.Year < row.Year && row.Location == MAIN_CHART_LOCATION){
        data_by_occupation[occupation].latest = row;
      }
    })

    var data_by_occupation_arr = [];
    var occ_keys = d3.map(data_by_occupation).keys();
    occ_keys.forEach((key) => {
      // get total diff - will be used for red-green coloring
      data_by_occupation[key].totalChangeDiff = -util.percentChange(data_by_occupation[key].latest.Value, data_by_occupation[key].earliest.Value);
      // get domain values for big chart x scale
      var lastYearValue = data_by_occupation[key].latest.Value;

      if(!isNaN(lowestDiffValue)
        && !isNaN(data_by_occupation[key].totalChangeDiff)
      ){
        lowestDiffValue = Math.min(lowestDiffValue, data_by_occupation[key].totalChangeDiff);
        highestDiffValue = Math.max(highestDiffValue, data_by_occupation[key].totalChangeDiff);

        lowestLastYearValue = Math.min(lowestLastYearValue, lastYearValue);
        highestLastYearValue = Math.max(highestLastYearValue, lastYearValue);
      }

      // console.log('lowestLastYearValue', lowestLastYearValue);
      // console.log('highestLastYearValue', highestLastYearValue);
      lowestOverallValue = Math.min(lowestOverallValue, data_by_occupation[key].earliest.Value);
      lowestOverallValue = Math.min(lowestOverallValue, data_by_occupation[key].latest.Value);

      highestOverallValue = Math.max(highestOverallValue, data_by_occupation[key].earliest.Value);
      highestOverallValue = Math.max(highestOverallValue, data_by_occupation[key].latest.Value);

      var lowestValue_for_occ = util.MAX_SAFE_INTEGER;
      data_by_occupation[key].rows.forEach((r) => {
        lowestValue_for_occ = Math.min(lowestValue_for_occ, r.Value)
      });
      data_by_occupation[key].lowestValue = lowestValue_for_occ;
      data_by_occupation_arr.push(data_by_occupation[key]);
    })

    var new_data_by_occupation_arr = _.remove(data_by_occupation_arr, (d) => 
      d.rows.length > 1 && (!isNaN(d.lowestValue))
    )

    // data_by_occupation_arr is the filtered out ones.
    var occ_keys_removed = _.remove(occ_keys, (occ_key) => {
      return _.find(data_by_occupation_arr, {name: occ_key}) 
    });

    var out = {
      occ_keys: occ_keys,
      data_by_occupation_arr: new_data_by_occupation_arr,
      lowestLastYearValue: lowestLastYearValue,
      highestLastYearValue: highestLastYearValue,
      data_by_occupation: data_by_occupation,
      lowestDiffValue: lowestDiffValue,
      highestDiffValue: highestDiffValue,
    }
    return out;
  },

  people_count_by_occupation: function(cb){
    if(cache.people_count_by_occupation){
      setTimeout(() => {
        cb(cache.people_count_by_occupation);
      }, 50)
    }else{
      // occupations count always csv
      d3.csv(exp.url_prefix+'/occupations_count.csv', (data) => {
        data = data
          .map((d) => {
            d.Value = parseFloat(d.Value);
            d.Year = parseFloat(d.Year);
            var iname = d['Indicator Name'];
            if(iname == 'All'){
              iname = 'All Occupations';
            }
            if(iname == 'All occupations'){
              iname = 'All Occupations';
            }
            d['Indicator Name'] = iname;
            return d;
          })
          .filter(d => { return !isNaN(d.Value) })
        ;
        cache.people_count_by_occupation = data;
        cb(data);
      });
    }
  },

  get_genders_data: (cb) => {
    if(cache.genders_data){
      setTimeout(() => {
        cb(cache.genders_data);
      }, 50)
    }else{
      exp.fetcher(exp.url_prefix+'/occupations_earnings_by_gender.'+exp.data_ext, (d) => {
        if(exp.data_json_format){
          d = exp.toCSVLikeGenderEarnings(d);
        }
        d = d.map((r) => {
          r.Value = parseFloat(r.Value, 10);
          r.Year = parseFloat(r.Year, 10);
          return r;
        })
        d = d.filter((r) => r.Value > 0)
        cache.genders_data = d;
        cb(cache.genders_data);
      });
    }
  },

  people_count_domain: function(
    people_count_by_occupation_filtered, /* only selected occ */
    people_count_by_occupation,
    data_all_occupations,
    selected_occupation_name
  ){

    function getEmpCt2(src, yr, loc, smallest){
      var rows = src.filter((r)=>{
        return r.Location == loc && r.Year == yr
      })
      if(smallest == true){
        return d3.min(rows.map((r) => r.Value))
      }else{
        return d3.max(rows.map((r) => r.Value))
      }
    }

    var domain_min = util.MAX_SAFE_INTEGER;
    var domain_max = util.MIN_SAFE_INTEGER;

    // domain from all occupations?
    var cross_domain_from_all_occ = false;

    var domain_from_occ = [];
    if(cross_domain_from_all_occ){
      domain_from_occ = data_all_occupations;
    }else{
      domain_from_occ = data_all_occupations.filter(
        occ => occ.name == selected_occupation_name
      )
    }

    domain_from_occ/* either all occupations or the selected one */
    .forEach(occ => {

      var people_count = people_count_by_occupation
        .filter((r) => r['Indicator Name'] == selected_occupation_name);

      var lastyear = 2018;

      var emp_count_city_2010 = getEmpCt2(people_count, 2010, util.loc_city);
      var emp_count_us_2010 = getEmpCt2(people_count, 2010, util.loc_us);
      var emp_count_state_2010 = getEmpCt2(people_count, 2010, util.loc_state);
      var emp_count_city_lastyear = getEmpCt2(people_count, lastyear, util.loc_city, false);
      var emp_count_us_lastyear = getEmpCt2(people_count, lastyear, util.loc_us, false);
      var emp_count_state_lastyear = getEmpCt2(people_count, lastyear, util.loc_state, false);

      var emp_increase_pct_city = util.percentChange(emp_count_city_2010, emp_count_city_lastyear)
      var emp_increase_pct_us = util.percentChange(emp_count_us_2010, emp_count_us_lastyear)
      var emp_increase_pct_state = util.percentChange(emp_count_state_2010, emp_count_state_lastyear)

      var min = d3.min([0, emp_increase_pct_city, emp_increase_pct_us, emp_increase_pct_state]);
      var max = d3.max([0, emp_increase_pct_city, emp_increase_pct_us, emp_increase_pct_state]);
      domain_min = Math.min(domain_min, min);
      domain_max = Math.max(domain_max, max);
    });

    if(-domain_max < domain_min){
      domain_min = -domain_max;
    }else{
      domain_max = -domain_min;
    }
    return [domain_min, domain_max];
  },


  salary_domain: function(
    data_all_occupations,
    selected_occupation_name
  ){

    function filter(src, yr, loc, smallest){
      var rows = src.filter((r)=>{
        return r.Location == loc && parseFloat(r.Year, 10) == yr
      })
      if(smallest == true){
        return d3.min(rows.map((r) => r.Value))
      }else{
        return d3.max(rows.map((r) => r.Value))
      }
    }

    var domain_min = util.MAX_SAFE_INTEGER;
    var domain_max = util.MIN_SAFE_INTEGER;

    // domain from all occupations?
    var cross_domain_from_all_occ = false;

    var domain_from_occ = [];
    if(cross_domain_from_all_occ){
      domain_from_occ = data_all_occupations;
    }else{
      domain_from_occ = data_all_occupations.filter(
        occ => occ.name == selected_occupation_name
      )
    }

    domain_from_occ/* either all occupations or the selected one */
    .forEach(occ => {

      var lastyear = 2018;


      var salary_city_2010 = filter(occ.rows, 2010, util.loc_city);
      var salary_us_2010 = filter(occ.rows, 2010, util.loc_us);
      var salary_state_2010 = filter(occ.rows, 2010, util.loc_state);
      var salary_city_lastyear = filter(occ.rows, lastyear, util.loc_city, false);
      var salary_us_lastyear = filter(occ.rows, lastyear, util.loc_us, false);
      var salary_state_lastyear = filter(occ.rows, lastyear, util.loc_state, false);

      var salary_increase_pct_city = util.percentChange(salary_city_2010, salary_city_lastyear)
      var salary_increase_pct_us = util.percentChange(salary_us_2010, salary_us_lastyear)
      var salary_increase_pct_state = util.percentChange(salary_state_2010, salary_state_lastyear)

      var min = d3.min([0, salary_increase_pct_city, salary_increase_pct_us, salary_increase_pct_state]);
      var max = d3.max([0, salary_increase_pct_city, salary_increase_pct_us, salary_increase_pct_state]);
      domain_min = Math.min(domain_min, min);
      domain_max = Math.max(domain_max, max);
    });

    if(-domain_max < domain_min){
      domain_min = -domain_max;
    }else{
      domain_max = -domain_min;
    }
    return [domain_min, domain_max];
  },
  toCSVLikeGenderEarnings: (d) => {
    var out = [];
    var csv_headers = [
      'Location',
      'Year',
      'Gender',
      'Indicator Name',
      'Indicator Description',
      'Indicator Group',
      'Value',
      'Value_Error',
    ]

    for(var i = 0; i < d.rows.length; i++){
      var item = {};
      var skip_data = false;
      var row = d.rows[i];

      row[3] = mapCategory(row[3], row[4], row[5]);

      for(var j = 0; j < csv_headers.length; j++){
        var header_key = csv_headers[j];
        var value = row[j];

        if(header_key == 'Location'){
          value = normalizeUSName(value);
          value = normalizeStateName(value);
        }
        if(header_key == 'Indicator Name'){
          if(value == 'All'){
            value = 'All Occupations';
          }
          if(value == 'All occupations'){
            value = 'All Occupations';
          }
        }
        if(header_key == 'Value'){
          if(isNaN(value)){
            skip_data = true;
          }
        }
        item[header_key] = value

        if(header_key == 'Year' && (value+'').indexOf('-') > -1){
          skip_data = true;
        }
      }

      if(!skip_data){
        out.push(item);
      }
    }
    return out;
  },
  toCSVLikeOccupationsWages: (d) => {
    var out = [];
    var csv_headers = [
      'Location',
      'Year',
      'Indicator Name',
      'Indicator Description',
      'Indicator Group',
      'Value',
      'Value_Error',
    ]


    for(var i = 0; i < d.rows.length; i++){
      var item = {};
      var row = d.rows[i];
      var skip_data = false;

      row[2] = mapCategory(row[2], row[3], row[4]);

      for(var j = 0; j < csv_headers.length; j++){
        var header_key = csv_headers[j];
        var value = row[j];


        if(header_key == 'Location'){
          value = normalizeUSName(value);
          value = normalizeStateName(value);
        }
        if(header_key == 'Indicator Name'){
          if(value == 'All'){
            value = 'All Occupations';
          }
        }
        if(header_key == 'Value'){
          if(isNaN(value)){
            skip_data = true;
          }
        }

        item[header_key] = value

        if(header_key == 'Year' && (value+'').indexOf('-') > -1){
          skip_data = true;
        }
      }

      if(!skip_data){
        out.push(item);
      }
    }
    return out;
  },
}


module.exports = exp;
