var util = require('./util.js');
var _ = require('lodash');

var exp = {
  chart: (svgSelector, lines, margin, width_full, height_full, draw_labels) => {
    var width = width_full - margin.left - margin.right,
      height = height_full - margin.top - margin.bottom;

    var svg = d3
      .select(svgSelector)
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
      .html('')
      .append('g')
        .attr('transform',
              'translate(' + margin.left + ',' + margin.top + ')');

    var cross_color = util.color_sidepanel_cross_gray;

    // tri
    var defs = svg.append('svg:defs')
    defs
      .append('svg:marker')
      .attr('id', 'triangle_gray')
      .attr('refX', 3)
      .attr('refY', 3)
      .attr('markerWidth', 6)
      .attr('markerHeight', 6)
      .attr('orient', 'auto')
      .append('path')
      .attr('d', 'M 0 1 4 3 0 5')
      .style('fill', cross_color);

    //cross lines
    svg.append('line')
      .attr('x1', width/2)
      .attr('y1', height/2)
      .attr('x2', width/2)
      .attr('y2', 0)
      .attr('class', 'cross_line')
      .attr('stroke-width', 3)
      .attr('marker-end', 'url(#triangle_gray)');

    svg.append('line')
      .attr('x1', width/2)
      .attr('y1', height/2)
      .attr('x2', width)
      .attr('y2', height/2)
      .attr('class', 'cross_line')
      .attr('stroke-width', 3)
      .attr('marker-end', 'url(#triangle_gray)');

    svg.append('line')
      .attr('x1', width/2)
      .attr('y1', height/2)
      .attr('x2', width/2)
      .attr('y2', height)
      .attr('class', 'cross_line')
      .attr('stroke-width', 3)
      .attr('marker-end', 'url(#triangle_gray)');

    svg.append('line')
      .attr('x1', width/2)
      .attr('y1', height/2)
      .attr('x2', 0)
      .attr('y2', height/2)
      .attr('class', 'cross_line')
      .attr('stroke-width', 3)
      .attr('marker-end', 'url(#triangle_gray)');

    if(draw_labels){
      svg.append('text')
        .attr('x', width/2)
        .attr('y', -5)
        .attr('fill', '#838383')
        .html('Earnings')
        .attr('text-anchor', 'middle')

      svg.append('text')
        .attr('x', width)
        .attr('y', height/2 + 25)
        .attr('fill', '#838383')
        .html('Employees')
        .attr('text-anchor', 'middle')
    }

    svg.append('circle')
      .attr('cx', width/2)
      .attr('cy', height/2)
      .attr('r', 6)
      .attr('fill', cross_color)
      .style('stroke', util.color_sidepanel_cross_circle_stroke)
    ;

    if(draw_labels){
      svg.append('text')
        .attr('fill', util.color_sidepanel_cross_circle_stroke)
        .text('2010')
        .attr('transform', 'translate('+(width/2 - 42)+','+(height/2 + 20)+')')
    }

    lines = _.sortBy(lines, (a) => a.name == util.loc_city ? 1: 0)

    lines.forEach((line, i) => {

      defs
        .append('svg:marker')
        .attr('id', 'triangle_main_'+i)
        .attr('refX', 3)
        .attr('refY', 3)
        .attr('markerWidth', 6)
        .attr('markerHeight', 6)
        .attr('orient', 'auto')
        .append('path')
        .attr('d', 'M 0 1 4 3 0 5')
        .style('fill', line.color);

      if(line.location == util.loc_city && draw_labels){
        // extend distance, and add text label
        svg.append('circle')
          .attr('transform', 'translate('+line.x2+','+(line.y2)+')')
          .attr('fill', line.color)
          .attr('r', 7)
          .text('2018')

        var text = svg.append('text')
          .attr('fill', line.color)
          .text('2018')

        var text_label_w = 36;
        if(line.x2+30+text_label_w > width){
          text
            .attr('transform', 'translate('+(line.x2-(10+text_label_w))+','+(line.y2+6)+')')
        }else{
          text
            .attr('text-anchor', 'middle')
            .attr('transform', 'translate('+(line.x2+30)+','+(line.y2+6)+')')
        }

        var polar = util.cartesian2Polar(line.x2-line.x1, line.y2-line.y1);
        if(polar.distance > 15){
          polar.distance -= 15;
        }
        // shorten arrow
        var new_pointer_coords = util.polar2Cartesian(polar.distance, polar.radians);
        line.x2 = line.x1 + new_pointer_coords.x;
        line.y2 = line.y1 + new_pointer_coords.y;

      }

      // line indicator
      svg.append('line')
        // .attr('data-legend', line.name)
        .attr('x1', line.x1)
        .attr('y1', line.y1)
        .attr('x2', line.x2)
        .attr('y2', line.y2)
        .attr('class', 'cross_line_main')
        .attr('fill', 'none')
        .attr('stroke', line.color)
        .attr('stroke-width', 3)
        .attr('marker-end', 'url(#triangle_main_'+i+')');
    });

  },


}

module.exports = exp;
