var exp = {
  init: () => {
    d3.select('.tab-1')
      .on('click', () => {
        exp.selectTab(1);
      })
    d3.select('.tab-2')
      .on('click', () => {
        exp.selectTab(2);
      })
    d3.select('.tab-3')
      .on('click', () => {
        exp.selectTab(3);
      })
    exp.selectTab(1); //initial open tab
  },
  selectTab: (i) => {
    d3.selectAll('.chart-tab').classed('selected', false);
    d3.select('.tab-'+i).classed('selected', true);
    d3.select('.tab-'+i).classed('selected', true);
    d3.select('.page').html('');
    switch(i){
      case 1:
        require('./chart_occupation_trends.js').init();
        break;
      case 2:
        require('./chart_population_earnings_map.js').init();
        break;
      case 3:
        require('./chart_wage_gap.js').init();
        break;
      default:
        throw new Error('error message')
    }
  },
}
module.exports = exp;
