var exp = {

  loc_city: window.location_city_name,
  loc_city_name: window.location_city_name,

  loc_state: window.location_state_names[0],
  loc_state_name: window.location_state_name_short,

  loc_us: 'US',
  loc_us_name: 'U.S.',

  color_sidepanel_green: '#548235',
  color_sidepanel_focus_secondary: '#595959', // gray
  color_sidepanel_focus_secondary_light: '#838383', // light gray, for non BC top label
  color_sidepanel_cross_circle_stroke: '#1d9db9', // light-blue
  color_sidepanel_cross_highlight:  '#1d9db9', // light-blue
  color_sidepanel_cross_gray:  '#ccc',

  color_wage_gap_red: '#e81d64',
  color_wage_gap_blue: '#00b0f0',

  formatPrice: function(price, withSymbol) {
    var out = price && price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    if(withSymbol){
      return '$'+out;
    }else{
      return out;
    }
  },
  formatPct: (pct) => {
    return pct.toFixed(2);
  },

  strsearch: function(str, needle){
    str=(str+'').toLowerCase();
    needle=(needle+'').toLowerCase();
    return str.indexOf(needle) > -1
  },

  last: (arr) => {
    return arr[arr.length-1];
  },

  percentChange: (valA, valB) => {
    var increase = valB - valA;
    var pct_increase = increase / valA * 100;
    return pct_increase;
  },

  textWrap: function(text, width) {
    // based on https://bl.ocks.org/mbostock/7555321
    text.each(function() {
      var text = d3.select(this),
          words = text.text().split(/\s+/).reverse(),
          line = [],
          lineNumber = 0,
          lineHeight = 1.1, // ems
          y = text.attr('y'),
          dy = parseFloat(text.attr('dy')) || 0,
          tspan = text.text(null).append('tspan').attr('x', 0).attr('y', y).attr('dy', dy + 'em');
      var word = words.pop();
      while (word) {
        word = words.pop();
        line.push(word);
        tspan.text(line.join(' '));
        if (tspan.node().getComputedTextLength() > width) {
          line.pop();
          tspan.text(line.join(' '));
          line = [word];
          tspan = text.append('tspan')
            .attr('x', 0)
            .attr('y', y)
            .attr('dy', ++lineNumber * lineHeight + dy + 'em').text(word);
        }
      }
    });
  },

  cartesian2Polar: function(x, y){
    var distance = Math.sqrt(x*x + y*y)
    var radians = Math.atan2(y, x) //This takes y first
    var degrees = radians * (180/Math.PI)
    return { distance: distance, radians: radians, degrees: degrees }
  },

  polar2Cartesian: function (r, angle_radians) {
    return {
      x: r * Math.cos(angle_radians),
      y: r * Math.sin(angle_radians)
    }
  },

  MAX_SAFE_INTEGER: (Number.MAX_SAFE_INTEGER  || 9007199254740991), // IE compatibility
  MIN_SAFE_INTEGER: (Number.MIN_SAFE_INTEGER  || -9007199254740991), // IE compatibility
}
module.exports = exp;
