var util = require('./util.js');
var _ = require('lodash');
var Data = require('../src/data_access.js');
var curveType = d3.curveCatmullRom;
var parseTime = d3.timeParse('%Y');
var cmp_vertiline = require('./cmp_vertiline_chart.js');

var exp = {
  is_open: false,

  open: (selected, onclose) => {
    var selected_name = selected['Indicator Name'] || selected.name;

    d3.selectAll('.sidepanel')
      .classed('open', true)

    d3.selectAll('.sidepanel .maintitle')
      .text(selected_name)

    exp.onclose = onclose;
    d3.selectAll('.sidepanel .close')
      .on('click', exp.close)

    Data.get_genders_data((genders_data) => {
      setTimeout(() => {
        exp.chart(selected_name, genders_data);
      }, 50);
    })
  },

  chart:(selected_name, gender_gap_data) => {
    var data_m = _.filter(gender_gap_data, {Gender: 'Male', 'Indicator Name': selected_name});
    var data_f = _.filter(gender_gap_data, {Gender: 'Female', 'Indicator Name': selected_name});

    exp.chart_line(
      selected_name, gender_gap_data,
      data_m, data_f
    );

    exp.chart_vertiline(
      selected_name, gender_gap_data
    );
  },

  chart_vertiline:  (selected_name, gender_gap_data) => {
    var gender_gap_data_occ_f = _.filter(gender_gap_data, {'Indicator Name': selected_name});

    var boundingBox = d3.select('.sidepanel .sidepanel_chart1').node().getBoundingClientRect();

    var margin = {top: 50, right: 20, bottom: 100, left: 60},
      width_full = boundingBox.width,
      height_full = Math.max(500, (boundingBox.height)),
      width = width_full - margin.left - margin.right,
      height = height_full - margin.top - margin.bottom;

    var count_lines = 3;
    var lines_with_both_genders = [
      { location: util.loc_city, label: util.loc_city_name },
      { location: util.loc_state, label: util.loc_state_name },
      { location: util.loc_us, label:util.loc_us_name },
    ];
    lines_with_both_genders = lines_with_both_genders.map((d, i) => {
      d.male = _.find(gender_gap_data_occ_f, {Gender: 'Male', Year: 2018, Location: d.location})
      d.female = _.find(gender_gap_data_occ_f, {Gender: 'Female', Year: 2018, Location: d.location})
      var median = Object.assign({}, d.male)
      median.Value = d3.median([d.male.Value, d.female.Value]);
      median.Gender = 'median';
      d.median = median
      d.occ_idx = d.male.occ_idx = d.female.occ_idx = d.median.occ_idx = i;
      d.Location = d.location;
      d.name = d.Location;
      return d;
    })

    var lines_flat = _.flatten(_.map(lines_with_both_genders, d => [d.median, d.male, d.female]))
    var values = _.map(lines_flat, d => d.Value)

    var xPosition_linear = d3.scaleLinear()
      .range([50, width]) // shift a bit to the right, so label doesnt get squished
      .domain([0, count_lines]);
    var xPosition = xPosition_linear;

    var yPosition = d3.scaleLinear()
      .rangeRound([0, height])
      .domain([
        d3.max(values) + 5000,
        d3.min(values) - 5000
      ]);

    var sidepanel = false;
    var parentSelector = '.sidepanel .sidepanel_chart1';
    d3.select(parentSelector).html('<div class="tooltip_container"></div>')
    var svg = d3.select('.sidepanel .sidepanel_chart1')
      .append('svg')
      .attr('width', width_full)
      .attr('height', height_full)
      .append('g')
        .attr('transform',
              'translate(' + margin.left + ',' + margin.top + ')');
    ;

    var for_sidepanel_loc_chart = true;
    cmp_vertiline.chart(
      parentSelector, svg, boundingBox, xPosition, yPosition,
      margin, width_full, height_full,
      sidepanel, sidepanel,
      lines_flat, lines_with_both_genders,
      exp.openSidepanel, false, for_sidepanel_loc_chart
    );
  },

  chart_line: (selected_name, gender_gap_data,
      data_m, data_f
  ) => {
    data_m = _.filter(data_m, {Location: util.loc_city})
    data_f = _.filter(data_f, {Location: util.loc_city})

    var data_m_by_year = _.groupBy(data_m, (d) => d.Year);
    var data_f_by_year = _.groupBy(data_f, (d) => d.Year);

    var width_guide = d3.select('.sidepanel .sidepanel_chart2').node().getBoundingClientRect();

    var margin = {top: 30, right: 70, bottom: 70, left: 20},
      width_full  = width_guide.width,
      width = width_full - margin.left - margin.right,
      height = 340 - margin.top - margin.bottom;

    // set the ranges
    var x = d3.scaleTime()
        .range([0, width])

    var y = d3.scaleLinear()
        .range([height, 0]);

    var svg = d3
      .select('.sidepanel .sidepanel_chart2 svg')
        .attr('width', width_full)
        .attr('height', height + margin.top + margin.bottom)
      .html('')
      .append('g')
        .attr('transform',
              'translate(' + margin.left + ',' + margin.top + ')');

    // Scale the range of the data
    x.domain([2010, 2018].map((d) => parseTime(''+d) ));
    var y_values = d3.extent(
      _.map(
        _.filter(gender_gap_data, (d) => d['Indicator Name'] == selected_name)
      , (d) => parseFloat(d.Value)).concat([0])
    )
    y.domain(y_values);

    var datum_m = Object.keys(data_m_by_year).map((key)=> {
      return data_m_by_year[key][0];
    });
    var datum_f = Object.keys(data_f_by_year).map((key)=> {
      return data_f_by_year[key][0];
    });
    svg.append('path')
      .datum(datum_m)
      .attr('name', 'data_us')
      .attr('fill', 'none')
      .attr('stroke', util.color_wage_gap_blue)
      .attr('stroke-width', 1.5)
      .attr('d', (d) => {
        return d3.line()
          .curve(curveType)
          .x(function(di) {
            return x(parseTime(di.Year))
          })
          .y(function(di) {
            return y(parseFloat(di.Value))
          })(d)
      })

    svg.append('path')
      .attr('name', 'data_f')
      .attr('fill', 'none')
      .attr('stroke', util.color_wage_gap_red)
      .attr('stroke-width', 1.5)
      .attr('d', (d) => {
        return d3.line()
          .curve(curveType)
          .x(function(di) {
            return x(parseTime(di.Year))
          })
          .y(function(di) {
            return y(parseFloat(di.Value))
          })(datum_f)
      })

    // Create the circle that travels along the curve of chart
    var groups = [
      { name: 'Male', data: datum_m },
      { name: 'Female', data: datum_f },
    ];

    groups.forEach((group, i) => {
      group.focus = svg
        .append('g')
        .append('circle')
          .attr('stroke',
             util.color_sidepanel_focus_secondary
          )
          .attr('r', 4.5)
          .attr('fill', 'white')
          .style('opacity', 0)

      // Create the text that travels along the curve of chart
      group.labelText = svg
        .append('g')
        .style('opacity', 0);

      var label_x_offset = 170;
      var label_y_offset = height + 30;
      group.labelText
        .append('text')
          .attr('x', 100*i + label_x_offset)
          .attr('y', 20 + label_y_offset)
          .attr('class', 'label_name')
          .attr('text-anchor', 'middle')
          .attr('alignment-baseline', 'middle')
          .style('font-size', '1rem')
          .attr('fill',
            util.color_sidepanel_focus_secondary_light
          )
          .html(group.name);
      ;

      group.labelText
        .append('text')
          .attr('x', 100*i + label_x_offset)
          .attr('y', label_y_offset)
          .attr('class', 'label_value')
          .attr('text-anchor', 'middle')
          .style('font-size', '1rem')
          .attr('alignment-baseline', 'middle')
          .attr('fill',
            util.color_sidepanel_focus_secondary_light
          )
          .html(0);
      ;
    })

    // -------------------------------------------------------------------------
    // X axis mouseover label

    function initMouseOverYear(){
      var hovered_year = 2011;

      var yearTooltip = svg.append('g')
        .attr('class', 'yearTooltip')
        .style('opacity', 0)
        .attr('transform', 'translate('+ x(parseTime(hovered_year)) +',0)');

      yearTooltip
        .append('text')
        .attr('class', 'year_text')
        .attr('y', height + 18)
        .attr('text-anchor', 'middle')
        .style('font-size', '0.85rem')
        .style('font-weight', '400')
        .style('font-family', 'sans-serif')
        .style('color', '#212529')
        .text('2011')


      yearTooltip
        .append('path')
        .attr('stroke', util.color_sidepanel_focus_secondary)
        .style('stroke-dasharray', ('1, 1'))
        .attr('stroke-width', 1.5)
        .attr('stroke-width', 1.5)
        .attr('d', () => {
          var pos_1 = [0, 0];
          var pos_2 = [0, height];
          return d3.line()([pos_1, pos_2])
        })
    }

    function drawMouseOverYearLabel(year_i){
      var hovered_year = 2010+year_i;
      var yearTooltip = svg.select('.yearTooltip')
        .attr('transform', 'translate('+ x(parseTime(hovered_year)) +',0)');
      if(year_i == 0){
        svg.select('.yearTooltip').style('opacity', 0);
        return
      }else{
        svg.select('.yearTooltip').style('opacity', 1);
      }

      if(hovered_year > 2010 && hovered_year < 2018){
        yearTooltip.select('.year_text')
          .attr('opacity', 1)
          .text(hovered_year)
      }else{
        yearTooltip.select('.year_text')
          .attr('opacity', 0)
          .text(hovered_year)
      }

    }

    initMouseOverYear();


    // -------------------------------------------------------------------------
    // Add the X Axis
    svg.append('g')
      .attr('name', 'axis_bottomx')
      .attr('class', 'axis_bottomx')
      .style('font', '0.8rem sans-serif')
      .attr('transform', 'translate(0,' + height + ')')
      .call(d3.axisBottom(x).tickValues([parseTime(2010), parseTime(2018)]));

    // Add the Y Axis
    svg.append('g')
      .attr('name', 'axis_lefty')
      .call(d3.axisLeft(y).ticks(0));

    // Create a rect on top of the svg area: this rectangle recovers mouse position
    svg
      .append('rect')
      .style('fill', 'none')
      .style('pointer-events', 'all')
      .attr('width', width)
      .attr('height', height)
      .on('mouseover', mouseover)
      .on('mousemove', mousemove)
      .on('mouseout', mouseout);


    // line labels on the right
    // -------------------------------------------------------------------------

    groups.forEach((group, i) => {
      group.targetY = group.y = y(util.last(group.data).Value) - 5;
      group.fx = 0;
    });

    // Define a custom force
    const forceClamp = (min, max) => {
      let nodes;
      const force = () => {
        nodes.forEach(n => {
          if (n.y > max) n.y = max;
          if (n.y < min) n.y = min;
        });
      };
      force.initialize = (_) => {
        nodes = _
      };
      return force;
    }
    var labelHeight = 20;
    // Set up the force simulation
    const force = d3.forceSimulation()
      .nodes(groups)
      .force('collide', d3.forceCollide(labelHeight / 2))
      .force('y', d3.forceY(d => d.targetY).strength(1))
      .force('clamp', forceClamp(-15, height))
      .stop();

    // Execute the simulation
    for (let i = 0; i < 300; i++) force.tick();

    groups.forEach((group, i) => {

      var isCity = group.name == util.loc_city
      group.x = x((parseTime(''+2018))) + (isCity ? 0: 5)
      svg
        .append('text')
        .attr('text-anchor',
          isCity ? 'middle' : 'start'
        )
        .style('pointer-events', 'none')
        .style('font-size', '1rem')
        .style('font-weight', 'bold')
        .attr('fill', group.name == 'Male' ? util.color_wage_gap_blue : util.color_wage_gap_red)
        .attr('x', group.x )
        .attr('y', group.y )
        .html(group.name)
    });

    function mouseover(){
      svg.select('.yearTooltip').style('opacity', 1);
      groups.forEach((group) => {
        group.focus.style('opacity', 1)
        group.labelText.style('opacity', 1)
      });
    }

    var bisect = d3.bisector(function(d) { return parseTime(d.Year); }).right;
    var bisectYear = d3.bisector(function(d) { return d }).right;

    function mousemove(){
      // recover coordinate we need
      var mousex = d3.mouse(this)[0] - (x(parseTime(2011))/2)
      var x0 = x.invert(mousex);
      var i = 0;
      var fakeGroup = _.range(2010, 2018).map((d) => parseTime(d))
      var i_year = bisectYear(fakeGroup, x0, 0);
      groups.forEach((group) => {
        i = bisect(group.data, x0, 0);
        var selectedData = group.data[i];

        if(!selectedData){
          return;
        }
        group.focus
          .attr('cx', x(parseTime(selectedData.Year)))
          .attr('cy', y(selectedData.Value))
        group.labelText.selectAll('.label_value')
          .html(util.formatPrice(selectedData.Value, true));
      });
      drawMouseOverYearLabel(i_year);
    }

    function mouseout(){
      svg.select('.yearTooltip').style('opacity', 0);
      groups.forEach((group) => {
        group.focus.style('opacity', 0)
        group.labelText.style('opacity', 0)
      });
    }

  },

  close: () => {
    d3.select('.sidepanel')
      .classed('open', false)
    exp.is_open = false;
    if(exp.onclose) {
      exp.onclose();
    }
  },

}

module.exports = exp;
