var util = require('./util.js');
var _ = require('lodash');
var Data = require('../src/data_access.js');
var sidepanel = require('./chart_wage_gap_sidepanel.js');
var cmp_vertiline = require('./cmp_vertiline_chart.js');

var exp = {
  init: function(){
    var page_template = require('../templates/chart_wage_gap.html');
    d3.select('.page').html(page_template);
    d3.select('.sidepanel .trend_title').text(util.loc_city_name + ' Trend');
    sidepanel.is_open = false;
    Data.get_genders_data((genders_data) => {
      setTimeout(() => {
        exp.chart(genders_data);
      }, 50);
    })
  },

  chart: function(genders_data){
    genders_data = genders_data.filter(r => r.Location == util.loc_city && r.Year == 2018)

    var boundingBox = d3.select('body').node().getBoundingClientRect(); //for svg use .getBBox();
    var headerBox = d3.select('.chart-tabs').node().getBoundingClientRect();
    var margin = {top: 90, right: 70, bottom: 100, left: 100},
      width_full = boundingBox.width,
      height_full = Math.max(500, (boundingBox.height-headerBox.height)),
      width = width_full - margin.left - margin.right,
      height = height_full - margin.top - margin.bottom;

    var svg = d3.select('.chart_main')
        .append('svg')
        .attr('width', width_full)
        .attr('height', height_full)
      .append('g')
        .attr('transform',
              'translate(' + margin.left + ',' + margin.top + ')');

    var occupations_with_both_genders = [];
    // use only occupations which have date for both genders
    genders_data.forEach((row, i) => {
      let acc = 'Indicator Name'
      if(!_.find(occupations_with_both_genders, { name: row[acc]})){
        var m = _.find(genders_data, {Gender: 'Male', 'Indicator Name': row[acc]})
        var f = _.find(genders_data, {Gender: 'Female', 'Indicator Name': row[acc]})
        if(m && f){
          var occ_idx = occupations_with_both_genders.length;
          var median = Object.assign({}, m)
          median.Value = d3.median([m.Value, f.Value]);
          median.Gender = 'median';

          m.occ_idx = occ_idx;
          f.occ_idx = occ_idx;
          median.occ_idx = occ_idx;
          occupations_with_both_genders.push({ name: row[acc], male: m, female: f, median: median });
        }
      }
    });

    occupations_with_both_genders = _.sortBy(occupations_with_both_genders, (r) => {
      return d3.median([r.male.Value, r.female.Value])
    });

    // set indexes sorted
    occupations_with_both_genders.forEach((r, i) => {
      r.occ_idx = i;
      r.male.occ_idx = i;
      r.female.occ_idx = i;
      r.median.occ_idx = i;
    });


    var count_occupations = occupations_with_both_genders.length;
    var genders_flat = _.flatten(_.map(occupations_with_both_genders, d => [d.median, d.male, d.female]))
    var values = _.map(genders_flat, d => d.Value)

    var xPosition_linear = d3.scaleLinear()
      .range([0, width])
      .domain([0, count_occupations]);
    var xPosition = xPosition_linear;

    var yPosition = d3.scaleLinear()
      .range([0, height])
      .domain(d3.extent(values).reverse());

    var for_sidepanel_loc_chart = false;
    cmp_vertiline.chart(
      '.chart_main ', svg, boundingBox, xPosition, yPosition,
      margin, width_full, height_full, true,
      sidepanel.is_open, genders_flat, occupations_with_both_genders,
      exp.openSidepanel, true, for_sidepanel_loc_chart
    );

    exp.setupSearch(occupations_with_both_genders);
  },

  setupSearch: function(occupations_with_both_genders){

    function highlightMatching(matching){
      // highlight this line, fade other lines
      d3.selectAll('.chart_main .circle').classed('circle--searchhover', (d, i) => {
        var n = d['Indicator Name'];
        return matching.filter((m) => m.name == n).length > 0;
      }).classed('circle--searchfade', (d, i) => {
        var n = d['Indicator Name'];
        return matching.filter((m) => m.name == n).length == 0;
      });

      d3.selectAll('.chart_main .line').classed('line--searchhover', (d, i) => {
        var n = d['Indicator Name'];
        return matching.filter((m) => m.name == n).length > 0;
      }).classed('line--searchfade', (d, i) => {
        var n = d['Indicator Name'];
        return matching.filter((m) => m.name == n).length == 0;
      });
    }
    function removeColorization(){
      // turn off hover and fade effects
      d3.selectAll('.chart_main .circle')
        .classed('circle--searchhover', false)
        .classed('circle--searchfade', false);
      d3.selectAll('.chart_main .line')
        .classed('line--searchhover', false)
        .classed('line--searchfade', false);
    }

    function handleChange(value){
      var val = value;
      if(!value){
        val = this.value || '';
      }
      var matching = occupations_with_both_genders.filter((d) => {
        return util.strsearch(d.name, val);
      })
      if(val.trim().length == 0){
        removeColorization();
      }else{
        highlightMatching(matching);
      }
    }

    d3.select('#inputbox')
      .on('change', handleChange)
      .on('keydown', function(){ var a = arguments; setTimeout(() => {handleChange.apply(this, arguments)}, 5) })
  },

  openSidepanel: (d) => {
    sidepanel.open(d, exp.onCloseSidebar);
  },

  onCloseSidebar: () => {
  }

}

module.exports = exp;
